*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-family: "Trebuchet", "Arial", sans-serif;
  color: #000000;
}

body {
  width: 100%;
  height: 100%;
}

@font-face {
  font-style: normal;
  font-family: "Exo 2";
  font-display: swap;
  src: url("../fonts/exo2.0_extralight.woff2") format("woff2"), url("../fonts/exo2.0_extralight.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-family: "Trebuchet";
  font-display: swap;
  src: url("../fonts/TrebuchetMS.woff2") format("woff2"), url("../fonts/TrebuchetMS.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-family: "Trebuchet Bold";
  font-display: swap;
  src: url("../fonts/TrebuchetMS-Bold.woff2") format("woff2"), url("../fonts/TrebuchetMS-Bold.woff") format("woff");
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.products {
  position: relative;
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  padding: 20px;
  background: center url("./../img/background.jpg") #000000;
}
@media (min-resolution: 2dppx) {
  .products {
    background-image: url("./../img/background@2x.jpg");
  }
}
.products::before {
  position: absolute;
  content: "";
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0.00005) 49.88%, rgba(0, 0, 0, 0.5) 100%);
  order: 0;
}

.products__wrapper {
  position: relative;
  width: fit-content;
  max-width: 1280px;
  margin: 0 auto;
}
.products__wrapper h1 {
  display: block;
  font-family: "Exo 2", "Arial", sans-serif;
  text-align: center;
  font-size: 36px;
  line-height: 44px;
  font-weight: 400;
  color: #ffffff;
  margin: 0 auto;
  text-shadow: 0 1px 1px #000000;
}

.products__list {
  display: flex;
  flex-wrap: wrap;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0;
  list-style: none;
}

.products__item {
  width: fit-content;
  margin: 24px 40px;
}

.products__card {
  position: relative;
  height: 100%;
  width: min-content;
  min-width: 320px;
  padding: 17px 48px 259px;
  background-color: #f2f2f2;
  border: 4px solid #1698d9;
  border-radius: 12px;
  overflow: hidden;
  margin-bottom: 12px;
  cursor: pointer;
  clip-path: polygon(0% 43px, 43px 0%, 100% 0%, 100% 0%, 100% 0%, 100% 100%, 0% 100%, 0% 100%);
}
.products__card::before {
  content: "";
  position: absolute;
  left: -19px;
  top: 18px;
  background-color: #1698d9;
  width: 70px;
  height: 5px;
  transform: rotate(-45deg);
}
.products__card img {
  position: absolute;
  left: -28px;
  bottom: -92px;
}
.products__card p {
  font-size: 16px;
  line-height: 19px;
  color: #666666;
  margin: 0 0 5px;
}
.products__card h2 {
  font-size: 48px;
  line-height: 56px;
  margin: 0 0 0 -3px;
  letter-spacing: 1.7px;
}
.products__card h3 {
  font-size: 24px;
  line-height: 28px;
  margin: 0 0 15px -1px;
  letter-spacing: 0.6px;
  word-break: break-all;
}
.products__card ul {
  list-style: none;
  margin: 0;
  padding: 0;
  color: #666666;
  font-size: 14px;
  line-height: 16px;
}
.products__card ul span {
  font-weight: 700;
}
.products__card div {
  position: absolute;
  right: 12px;
  bottom: 12px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background-color: #1698d9;
  color: #ffffff;
}
.products__card div p {
  color: #ffffff;
  padding-top: 7px;
  font-size: 42px;
  line-height: 22px;
  margin: 5px 0;
}
.products__card div span {
  font-size: 21px;
}

.products__item p:last-child {
  display: block;
  width: fit-content;
  margin: 0 auto;
  font-size: 13px;
  line-height: 15px;
  color: #ffffff;
}
.products__item p:last-child label {
  position: relative;
  color: #1698d9;
  cursor: pointer;
}
.products__item p:last-child label::after {
  position: absolute;
  content: "";
  left: 1px;
  bottom: 0;
  width: calc(100% - 6px);
  height: 1px;
  border-bottom: 1px dashed #1698d9;
}

.products__checkbox ~ span {
  display: none;
}

.products__item:focus .products__card {
  border-color: #2ea8e6;
}
.products__item:focus .products__card::before {
  background-color: #2ea8e6;
}
.products__item:focus .products__card div {
  background-color: #2ea8e6;
}
.products__item:focus p label {
  color: #22a7e9;
}

.products__checkbox:hover + label .products__card {
  border-color: #2ea8e6;
}
.products__checkbox:hover + label .products__card::before {
  background-color: #2ea8e6;
}
.products__checkbox:hover + label .products__card div {
  background-color: #2ea8e6;
}
.products__checkbox:hover ~ p label {
  color: #22a7e9;
}

.products__checkbox:checked + label .products__card {
  border-color: #d91667;
}
.products__checkbox:checked + label .products__card::before {
  background-color: #d91667;
}
.products__checkbox:checked + label .products__card div {
  background-color: #d91667;
}
.products__checkbox:checked ~ p {
  display: none;
}
.products__checkbox:checked ~ span:nth-child(3) {
  display: block;
  width: fit-content;
  margin: 0 auto;
  font-size: 13px;
  line-height: 15px;
  color: #ffffff;
}

.products__checkbox:checked:hover + label .products__card {
  border-color: #e52e7a;
}
.products__checkbox:checked:hover + label .products__card::before {
  background-color: #e52e7a;
}
.products__checkbox:checked:hover + label .products__card div {
  background-color: #e52e7a;
}

.products__checkbox:checked:focus + label .products__card {
  border-color: #d91667;
}
.products__checkbox:checked:focus + label .products__card::before {
  background-color: #d91667;
}
.products__checkbox:checked:focus + label .products__card div {
  background-color: #d91667;
}

.products__checkbox:disabled + label .products__card {
  border-color: #b3b3b3;
  color: #b3b3b3;
}
.products__checkbox:disabled + label .products__card img,
.products__checkbox:disabled + label .products__card h2,
.products__checkbox:disabled + label .products__card h3 {
  opacity: 0.5;
}
.products__checkbox:disabled + label .products__card p,
.products__checkbox:disabled + label .products__card li {
  color: #b3b3b3;
  opacity: 0.5;
}
.products__checkbox:disabled + label .products__card div p {
  color: #ffffff;
  opacity: 1;
}
.products__checkbox:disabled + label .products__card div span {
  opacity: 1;
}
.products__checkbox:disabled + label .products__card::before {
  background-color: #b3b3b3;
}
.products__checkbox:disabled + label .products__card div {
  background-color: #b3b3b3;
}
.products__checkbox:disabled ~ p {
  display: none;
}
.products__checkbox:disabled ~ span:nth-child(4) {
  display: block;
  width: fit-content;
  margin: 0 auto;
  font-size: 13px;
  line-height: 15px;
  color: #ffff66;
}

@media (max-width: 1279px) {
  .products {
    width: 100%;
    min-width: 420px;
  }
  .products__list {
    justify-content: center;
  }
}
@media (max-width: 767px) {
  .products__wrapper h1 {
    font-size: 1.5rem;
    margin-bottom: 30px;
  }
  .products__list {
    flex-direction: column;
  }
  .products__item {
    width: 100%;
    margin: 0 0 40px;
  }
  .products__card {
    padding: 20px 30px 280px;
  }
}